import React from 'react';
import './style.css';

const imageData = [
    "data:image/png;base64,",
    "data:image/png;base64,",
    "data:image/png;base64,",
    "data:image/png;base64,",
    "data:image/png;base64,",
    "data:image/png;base64,",
    "data:image/png;base64,",
    "data:image/png;base64,",
    "data:image/png;base64,",
    "data:image/png;base64,",
    "data:image/png;base64,",
    "data:image/png;base64,",
    "data:image/png;base64,",
    "data:image/png;base64,",
    "data:image/png;base64,",
    "data:image/png;base64,",
]
export default class BallStatusController extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            ballStatus: [
                true,
                true,
                true,
                true,
                true,
                true,
                true,
                true,
                true,
                true,
                true,
                true,
                true,
                true,
                true,
                true,
            ]
        }
    }

    componentWillReceiveProps = (props) => {
        this.setState({
            ballStatus: props.data
        })
    }

    ballPressed = (index) => {
        let { ballStatus } = this.state;
        ballStatus[index] = false;
        this.setState({
            ballStatus
        })
        this.props.onBallPressed(index);
    }

    render () {
        const { ballStatus } = this.state;
        return (
            <div className="ball-status-controller">
                {
                    Array.isArray(ballStatus) && ballStatus.map((item, index) => {
                        return(
                            <img
                                className={"ball" + (!item?" inactive":"")}
                                key={index}
                                src={imageData[index]}
                                onClick={() => {
                                    if (item) {
                                        this.ballPressed(index)
                                    }
                                }}
                            />
                        )
                    })
                }
            </div>
        )
    }
}